<template>
    <div style="background: #fff;">
        <div style="padding-top: 55px;"></div>
        <div class="newDetail">
            <div class="detail-title">
                <strong class="ft-18 block" style="text-align: center;">{{ list.post_title }}</strong>
                <span class="fl">{{ list.post_source }}</span>
                <span class="fr">{{ list.published_time }}</span>
            </div>
            <div class="detail-content">
                <!--<div class="detail-con-desc">-->
                  <!---->
                <!--</div>-->
                <div class="detail-pic" v-html="list.post_content">
                  <!--<img src="../../../static/images/40.png" alt="">-->
                </div>
            </div>
            <!--<div class="pages">-->
                <!--<div class="">-->
                    <!--<el-button class="prve"></el-button>-->
                    <!--<span>2016</span>-->
                <!--</div>-->
                <!--<div class="">-->
                    <!--<el-button class="next"></el-button>-->
                    <!--<span>2016</span>-->
                <!--</div>-->
            <!--</div>-->
        </div>
    </div>
</template>

<script type="text/javascript">
  import ElButton from "../../../node_modules/element-ui/packages/button/src/button";
  export default {
    components: {
      ElButton
    },
    data() {
      return {
        list: [],
      }
    },
    created() {
      this.getNews();
    },
    computed: {

    },
    mounted() {

    },
    filters: {
      TimeTexts (value) {
        return value.substring(0, 10);
      },
    },
    methods: {
      getNews() {
        this.load_data = true;
        this.$ajax.HttpGet(this.$api.get_content.GET_NEWS).then((res) => {
          if (res.code == 0) {
            this.load_data = false;
            this.$message({
              message: res.msg,
              type: 'warning'
            });
          } else if (res.code == 1) {
            this.load_data = false;
            this.$message({
              message: res.msg,
              type: 'success'
            });
            for (let i in res.data) {
              if (res.data[i].id == this.$route.query.newid) {
                this.list = res.data[i];
                console.log(this.list);
              }
            }
          }
        });
      }
    },
  }
</script>


<style type="text/css">
  /* newDetail start */
  .newDetail {
    /*padding: 10px 0px;*/
    background: #fff;
    padding-bottom: 30px;
  }

  .newDetail .detail-title {
    width: 260px;
    height: 66px;
    margin: 0px auto 0px;
    line-height: 35px;
    overflow: hidden;
  }

  .newDetail .detail-title span {
    color: #ccc;
  }

  .newDetail .detail-content .detail-con-desc {
    margin: 60px 50px 0px;
    line-height: 30px;
    text-indent: 2em;
    padding-bottom: 20px;
  }

  .newDetail .detail-content .detail-pic {
    margin: 0px auto;
    /*width: 910px;*/
  }

  .detail-pic p {
    padding: 0px 5%;
  }

  .detail-pic p img {
    width: 100%;
  }

  .newDetail .pages {
    margin: 80px 0px 40px;
    height: 37px;
  }

  /* newDetail end */
</style>



